import React, { Fragment, useState, useEffect } from 'react';
import { Row, Col,Divider} from 'antd';
// import '../Home.css';

const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition
const mic = new SpeechRecognition()

mic.continuous = true
mic.interimResults = true
mic.lang = 'en-US'

function Advicebox (){
    const [isListening, setIsListening] = useState(false)
    const [note,setNote] = useState(null)
    const [savedNotes, setSavedNotes] = useState([])

    useEffect(() => {
        handleListen()
    }, [isListening])

    const handleListen = () => {
        if(isListening) {
            mic.start()
            mic.onend = () => {
                console.log('continue...')
                mic.start()
            }
        } else{
            mic.stop()
            mic.onend = () =>{
                console.log('stop')
            }
        }
        mic.onstart = ()=>{
            console.log('Recording...')
        }
        mic.onresult = event => {
            const transcript = Array.from(event.results)
            .map(result => result[0])
            .map(result => result.transcript)
            .join('')
            console.log(transcript)
            setNote(transcript)
            mic.onerror = event =>{
                console.log(event.error)
            }
        }
    }

    const handleSaveNote = () => {
        setSavedNotes([...savedNotes, note])
        setNote('')
    }

    return (
        <Fragment>
        <h2>You can use voice instead of typing</h2>
        <Row className="gutter-row" span={8} offset={1}>
            <div>
            {isListening ? <span>Status: On</span> :<span>Status: Off</span>}
            <button onClick = {handleSaveNote} disabled={!note}>Save</button>
            <button onClick = {() => setIsListening(prevState => !prevState)}>Start/Stop</button>
            </div>
            <div className = 'current'>
            <h6>Current Notes</h6>
            <p>{note}</p>
            </div>
           
    </Row>
        <Divider type = 'vertial'/>
    <Row className="gutter-row" span={8} offset={1}>
        <div className = 'confirmed'>
        <h6>Confirmed Notes</h6>
          {savedNotes.map(n => (
              <p key={n}>{n}</p>
          ))}
        </div>
    </Row>    
    </Fragment>
    );
}

export default Advicebox;