import { setText } from 'echarts/lib/util/graphic';
import React,{useState} from 'react';
import '../slider.scss';
import Img from './Img';
import i1 from '../data/pics/1.jpg';
import i2 from '../data/pics/2.jpg';
import i3 from '../data/pics/3.jpg';
import i4 from '../data/pics/4.jpg';
import i5 from '../data/pics/5.jpg';
import i6 from '../data/pics/6.jpg';


function Slider (){
    let sliderArr = [
    <Img src={i2} />,
    <Img src={i3} />,
    <Img src={i1} />,
    <Img src={i4} />,
    <Img src={i5} />,
    <Img src={i6} />

];
    const [x,setX] = useState(0);
    const goLeft=()=> {
        x === 0 ? setX(-100 * (sliderArr.length -1)) : setX(x+100);
    };

    const goRight = () => {
        x === -100 * (sliderArr.length -1) ? setX(0) : setX(x-100);
    };

    const style = {transform : `translateX(${x}%)`}
    // SASS, var in div ok

    return (
        <div className = 'slider'>
            {
                sliderArr.map((item, index) => {
                    return(
                        <div 
                        key = {index} 
                        className = 'slide' 
                        style = {style} >
                            {item}
                        </div>
                    );
            })}
       <button id='goLeft' onClick = {goLeft}>
         <i className="fas fa-chevron-circle-left"></i>
       </button>
       <button id='goRight' onClick = {goRight}>
         <i className="fas fa-chevron-circle-right"></i>
       </button>
        </div>
    );
}
export default Slider;
